import BadRequestException from "../exceptions/BadRequestException";

class RegexHelper {
    /**
     * *   .
     * *@param {string} field    css 
     * *@param {string} msg            
     */
  
    value(field, msg) {
      const content = field.value;
      if (
        content === undefined ||
        content === null ||
        (typeof content == "string" && content.trim().length === 0)
      ) {
        throw new BadRequestException(msg, field);
      }
      return true;
    }
  
    /**
     * *    
     * *@param {string} field    css 
     * *@param {int}    len        
     * *@param {string} msg            
     */
  
    minLength(field, len, msg) {
      this.value(field, msg);
  
      let content = field.value;
      if (content.trim().length < len) {
        throw new BadRequestException(msg, field);
      }
      return true;
    }
    /**
     * *    
     * *@param {string} field    css 
     * *@param {int}    len        
     * *@param {string} msg            
     */
    maxLength(field, len, msg) {
      this.value(field, msg);
  
      let content = field.value;
      if (content.trim().length > len) {
        throw new BadRequestException(msg, field);
      }
      return true;
    }
    /**
     * *   
     * *@param {string} field    css 
     * *@param {string} msg            
     * *@param {object} regexExpr  
     */
    field(field, msg, regexExpr) {
      this.value(field, msg);
  
      const content = field.value;
      const src = content.trim();
  
      if (!regexExpr.test(src)) {
        throw new BadRequestException(msg, field);
      }
      return true;
    }
    /**
     * *    
     * *@param {string} field   css 
     * *@param {string} msg           
     */
    phoneNum(field,msg){
      return this.field(field,msg,/^[0-9]{2,3}[0-9]{3,4}[0-9]{4}/);
    }
  
  
  
    /**
     * *  
     * *@param {string} field   css 
     * *@param {string} msg           
     */
    num(field, msg) {
      return this.field(field, msg, /^[0-9]*$/);
    }
    /**
     * *   
     * *@param {string} field   css 
     * *@param {string} msg           
     */
    eng(field, msg) {
      return this.field(field, msg, /^[a-zA-Z]*$/);
    }
    /**
     * *    
     * *@param {string} field   css 
     * *@param {string} msg           
     */
    engNum(field, msg) {
      return this.field(field, msg, /^[a-zA-Z0-9]*$/);
    }
    /**
     * *       
     * *@param {string} field   css 
     * *@param {string} msg           
     */
    regExpEngNum(field, msg) {
      return this.field(field, msg, /^[a-z0-9_-]*$/);
    }
    /**
     * *       
     * *@param {string} field   css 
     * *@param {string} msg           
     */
    allRegExpEngNum(field, msg) {
      return this.field(
        field,
        msg,
        /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]/
      );
    }
    /**
     * *  , 
     * *@param {string} field   css 
     * *@param {string} msg           
     */
    korEng(field, msg) {
      return this.field(field, msg, /^[a-z|A-Z|-|-]+$/);
    }

    /**
     * *   
     * *@param {string} field   css 
     * *@param {string} msg           
     */
     kor(field, msg) {
      return this.field(field, msg, /^[--]+$/);
    }

    /**
     * * 4  
     * *@param {string} field   css 
     * *@param {string} msg          
     */
    numYear(field, msg) {
      return this.field(field, msg, /^\d{4}$/);
    }
    /**
     * *   
     * *@param {string} field   css 
     * *@param {string} msg           
     */
    check(field, msg) {
        const checkedItem = Array.from(field).filter((v,i)=>v.checked);
    //   const content = field;
    //   const selectedOption = content.options[content.selectedIndex].value;
  
      // return alert(selectedOption);
    //   if (selectedOption === 0) {
    //     throw new BadRequestException(msg, field);
    //   }
      if (checkedItem.length === 0) {
        throw new BadRequestException(msg, field[0]);
      }
    }
  
    /**
     * * 1~ 31  
     * *@param {string} field   css 
     * *@param {string} msg           
     */
    numDay(seletor, msg) {
      return this.field(seletor, msg, /^(0[1-9]|[12][0-9]|3[01])$/);
    }
  
     /**
     * *    
     * *@param {string} field   css 
     * *@param {string} msg           
     */
    email(field, msg) {
      const content = field.value;
      if (content === "") {
        return true;
      } else {
        return this.field(
          field,
          msg,
          /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/i
        );
      }
    }
    /**
     * *    
     * *@param {string} origin   
     * *@param {string} compare  
     * *@param {string} msg           
     */
    compareTo(origin, compare, msg) {
      this.value(origin, msg);
      this.value(compare, msg);
  
      var src = origin.value.trim();
      var dsc = compare.value.trim();
  
      if (src !== dsc) {
        throw new BadRequestException(msg, origin);
      }
      return true;
    }
  }
  
  export default new RegexHelper();