import React from "react";
import "./assets/css/style.css";
import useAxios from "axios-hooks";
import regexHelper from "./libs/RegexHelper";

import Spinner from "./component/Spinner";

const  App=()=> {

/*   Ajax    -   */
  const [{loading},refetch] = useAxios({
      url:"http://localhost:3001/member",
      method:"POST"
  },{manual:true});

  const onSubmit = (e)=>{
      e.preventDefault();

      const current = e.target;
      try{
            regexHelper.value(current.userid," ");
            regexHelper.minLength(current.userid,2,"   ");
            regexHelper.maxLength(current.userid,20," 20  ");
            
            regexHelper.value(current.userpsw," ");
            regexHelper.minLength(current.userpsw,2,"   ");
            regexHelper.maxLength(current.userpsw,20," 20  ");

            regexHelper.value(current.userpswre,"  ");
            regexHelper.compareTo(current.userpsw,current.userpswre,"   ");

            regexHelper.value(current.username," ");
            regexHelper.minLength(current.username,2,"   ");
            regexHelper.maxLength(current.username,40," 40  ");

            regexHelper.value(current.useryear," ");
            regexHelper.num(current.useryear,"  ");
            regexHelper.maxLength(current.useryear,4," 4  ");

            regexHelper.value(current.usermonth," .");

            regexHelper.value(current.userday," ");
            regexHelper.num(current.userday,"  ");
            regexHelper.maxLength(current.userday,2," 2  ");

            regexHelper.email(current.useremail,"  ");

            regexHelper.phoneNum(current.usernum,"  ");

            regexHelper.value(current.usercountry," ");
            
            regexHelper.num(current.autonum,"  .");
            
      }catch(e){
          console.error(e);
          window.alert(e.message);
          e.field.focus();
          return;
      }
      let json = null;
      //,,  async ~await  .
      (async ()=>{
        try{
            const response = await refetch({
                data:{
                    userid : current.userid.value,
                    userpsw : current.userpsw.value,
                    username : current.username.value,
                    birthdayYear : parseInt(current.useryear.value),
                    birthdayMonth: parseInt(current.usermonth.value),
                    birthday:parseInt(current.userday.value),
                    gender : current.usergender.value,
                    email: current.useremail.value,
                    phonenumber:current.usernum.value,
                    country:current.usercountry.value
                }
            });
            json = response.data;
        }
        catch(e){
            console.error(e);
            window.alert(`
            ${e.message}`)
        }   

        // ?
        if(json !== null){
            window.alert("~")
        }
      })();
  };
  
  return (
      <>    
      <Spinner visible={loading}/>
        <div className="con">
            <h1>
                <span>
                    NAVER
                </span>
                <img src="https://ssl.pstatic.net/static/nname/join/m_logo_naver.png" alt=""/>
            </h1>
                <form onSubmit={onSubmit}>
                <div className="row">
                    <label className="title" htmlFor="userid"></label>
                    <span className="name-email">
                        <input type="text"  className="field"  name="userid" /><span>@naver.com</span>
                    </span>              
                    <label className="title field"  htmlFor="userpsw"></label>
                    <input type="password" name="userpsw"  />
                    <label className="title" htmlFor="userpswre"> </label>
                    <input type="password" className="field" name="userpswre"  /> 
                </div>
                <div className="row">
                    <label className="title" htmlFor="username"></label>
                    <input type="text" className="field" name="username"/>
                    <label className="title" htmlFor="useryear"></label>
                    <div className="b-day-field">
                        <input type="text" className="field" placeholder="(4)" name="useryear"/>
                        <select name="usermonth">
                            <option value="0"  > </option>
                            <option value="1"  > 1</option>
                            <option value="2"  > 2</option>
                            <option value="3"  > 3</option>
                            <option value="4"  > 4</option>
                            <option value="5"  > 5</option>
                            <option value="6"  > 6</option>
                            <option value="7"  > 7</option>
                            <option value="8"  > 8</option>
                            <option value="9"  > 9</option>
                            <option value="10" >10</option>
                            <option value="11" >11</option>
                            <option value="12" >12</option>
                        </select>
                        <input type="text" className="field" placeholder="" name="userday"/>
                    </div>
                    <label className="title" htmlFor="usergender"></label>
                    <select name="usergender">
                        <option value=""></option>
                        <option value="M"></option>
                        <option value="F"></option>
                        <option value="U"></option>
                    </select>
                </div>
                <div className="row">
                    <label className="title" htmlFor="usermail">  <span>()</span></label>
                    <input type="text" className="field" placeholder="" name="useremail"/>
                    <label className="title" htmlFor="user_num"></label>
                    <select name="usercountry">
                        <option value="82" > +82</option>
                        <option value="81" > +81</option>
                        <option value="86" > +86</option>
                    </select>
                    <div className="num-field">
                        <input type="text" name="usernum" className="field" placeholder=" "/>
                        <input type="submit" name="autoNumBtn"  className="btn-style field" value=" "/>
                    </div>
                    <input type="text" name="autonum" className="field" placeholder=" "/>
                </div>

                <button type="submit"  className="btn-style" name="btn1">
                    
                </button>
            </form>
        </div>
      </>
  );
};

export default React.memo(App);
